import '../oweek.css';
import './coords.css';

const coords = [
    {
        name: "Emily Duffus",
        year: "Junior",
        major: "Cognitive Sciences, Biochemistry, & Medical Humanities",
        from: "Glen Ridge, NJ"
    },
    {
        name: "Edesiri Mushale",
        year: "Junior",
        major: "Mechanical Engineering",
        from: "Vacaville, CA"
    },
    {
        name: "Olivia Morris",
        year: "Junior",
        major: "Cognitive Sciences",
        from: "Tampa, FL"
    }
]

const Coords = () => (
    <div className='oweek-page'>
        <h1 className='oweek-title'>Meet the Coords</h1>
        <div className='coords-bios'>
            {
                coords.map(({name, year, major, from}) => (
                    <div className='coord-bio' key={name}>
                        <h3>{name}</h3>
                        <p>{year}</p>
                        <p>{major}</p>
                        <p>{from}</p>
                    </div>
                ))
            }
        </div>
        <p></p>
    </div>
)

export default Coords;